.meta-box-col {
	display: flex;
	flex-direction: column;
	align-items: center;
	padding-top: 15px;

	label {
		flex-shrink: 0;
		width: 100%;
		margin-bottom: 10px;
		font-weight: 500;
	}

	textarea {
		width: 100%;
		height: 150px;
	}
}

.meta-box-row {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	padding: 10px 20px;

	label {
		width: 100px;
		font-weight: 500;
	}

	textarea {
		height: 150px;
	}

	.input-small {
		width: 100px;
	}

	.input-medium {
		width: 300px;
	}

	.input-large {
		width: 500px;
	}
}
