.meta-box-col {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 15px;
}
.meta-box-col label {
  flex-shrink: 0;
  width: 100%;
  margin-bottom: 10px;
  font-weight: 500;
}
.meta-box-col textarea {
  width: 100%;
  height: 150px;
}

.meta-box-row {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 10px 20px;
}
.meta-box-row label {
  width: 100px;
  font-weight: 500;
}
.meta-box-row textarea {
  height: 150px;
}
.meta-box-row .input-small {
  width: 100px;
}
.meta-box-row .input-medium {
  width: 300px;
}
.meta-box-row .input-large {
  width: 500px;
}